import React from "react";
import { Redirect } from "react-router-dom";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Profile from "routes/Profile";
import Auth from "../routes/Auth";
import Home from "../routes/Home";
import Navigation from "./Navigation";
import DoProko from "routes/DoProko";
import DoSoty from "routes/DoSoty";
import DoSakuga from "routes/DoSakuga";
import Special from "routes/Special";
import Adorka from "routes/Adorka";
import LineOfAction from "routes/LineOfAction";

const AppRouter=({isLoggedIn,userObj}) => {
    
    return (
        <Router>
            {isLoggedIn &&<Navigation />}
            <Switch>
                {isLoggedIn ?(
                    <>
                        <Route exact path= "/">
                            <Home userObj={userObj}/>
                        </Route>
                        <Route exact path= "/profile">
                            <Profile userObj={userObj}/>
                        </Route>
                        <Route exact path= "/doProko">
                            <DoProko userObj={userObj}/>
                        </Route>
                        <Route exact path= "/doSoty">
                            <DoSoty userObj={userObj}/>
                        </Route>
                        <Route exact path= "/doSakugaBooru">
                            <DoSakuga userObj={userObj}/>
                        </Route>
                        <Route exact path= "/special">
                            <Special userObj={userObj}/>
                        </Route>
                        <Route exact path= "/adorka">
                            <Adorka userObj={userObj}/>
                        </Route>
                        <Route exact path= "/lineofaction">
                            <LineOfAction userObj={userObj}/>
                        </Route>
                        <Redirect from={"*"} to={"/"} />
                    </>
                 ) : (
                     <>
                    <Route exact path= "/">
                          <Auth /> 
                    </Route>
                    <Redirect from={"*"} to={"/"} />
                    </>
                 )}
            </Switch>
        </Router>
    )
}
export default AppRouter; 