// taken from https://github.com/videojs/video.js/blob/master/docs/guides/react.md
import React from "react";
import videojs from "video.js";
import "video.js/dist/video-js.css"
//import '@douglassllc/videojs-framebyframe/videojs.framebyframe.css'
//import 'video.js/dist/video-js.css'
// videojs-framebyframe-plugin
//import "@douglassllc/videojs-framebyframe"
// videojs-framebyframe-plugin
 import "./videojs-skin.css"
 import framebyframe from './videojs-framebyframe/videojs.framebyframe.js'
// import './videojs-framebyframe/videojs.framebyframe.css'


 

 
export default class VideoPlayer extends React.Component {
  componentDidMount() {
    // instantiate video.js
    var registerPlugin = videojs.registerPlugin || videojs.plugin;
    registerPlugin('framebyframe', framebyframe);
    this.player = videojs(this.videoNode, this.props, function onPlayerReady() {
      console.log("onPlayerReady", this);
    });
    this.player.addClass('vjs-matrix');
    

    // if (this.videoNode) {
    //   this.videoNode.setAttribute("webkit-playsinline", true);
    //   this.videoNode.setAttribute("playsinline", true);
    // }
  }; 

  // destroy player on unmount
  componentWillUnmount() {
    if (this.player) {
      this.player.dispose();
    }
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.videoUrl !== this.props.videoUrl) {
      this.player.dispose();
      const videoJsOptions = {
        autoplay: true,
        controls: true,
        sources: [
          {
            src: this.props.videoUrl,
            type: "video/mp4",
          },
        ],
      };
      this.player = videojs(
        this.videoNode,
        videoJsOptions,
        this.props,
        function onPlayerReady() {
          console.log("onPlayerReady", this);
        }
      );
    }
  }

  // wrap the player in a div with a `data-vjs-player` attribute
  // so videojs won't create additional wrapper in the DOM
  // see https://github.com/videojs/video.js/pull/3856
  render() {
    return (
      <div data-vjs-player>
        <video ref={node => (this.videoNode = node)} className="video-js" />
      </div>
    );
  };
};