//import CCsubmit from "../components/Csubmit";
//import { v4 as uuidv4 } from 'uuid';
import { dbService ,storageService} from "fbase";
import React, { useEffect, useRef, useState,useCallback } from "react";
import CanvasDraw from "../../src/canvasDrawIndex";
import   firebase from "firebase/compat/app";
import Hotkeys from "react-hot-keys";

 const Home=({userObj}) => {
    const [Csubmit,setCsubmit]= useState("");
    const [Csubmits,setCsubmits]= useState([]);
    const [Line,setLine]= useState([]);
    const canvasRef = useRef();
    const [pinNum,setPinNum]=useState("");
    const [randomPinNum,setRandomPinNum]=useState([]);
    const [pinReload,setPinreload]=useState(0);
    const [pinID,setPinID]=useState(0);
    const [croquisTime,setCroquisTime]=useState(60);
    const allowPointerType=["all","mouse","touch","pen"]
    const [pointerTypeIndex,setPointerTypeIndex]=useState(3)
    //allowpointertype으로 터치허용유무설정가능 일단 펜만 가능하게함. 나중에하는걸로
        const [min,setMin]=useState(0);
        const [sec,setSec]=useState(0);
        const time=useRef(croquisTime);
        const timeId=useRef(null);
        const [drawSetting,setDrawSetting]=useState({
            croquisTimeDB:60,
            brushRadius:1,
            lazyRadius:1,
            eraseRadius:10,
            


        })
    const [timeSetBottonToggle,setTimeSetBottonToggle]=useState(false);


    const [brushColor,setBrushColor]=useState("#444")



    

    const getMyCroquisSettings = ()=>{
              dbService.doc("usersetting/"+userObj.uid).get().then((doc) => {
            if (doc.exists) {
                setDrawSetting({
                    brushRadius:doc.data().brushRadius,
                    lazyRadius:doc.data().lazyRadius,
                    eraseRadius:doc.data().eraseRadius,
                    croquisTimeDB:doc.data().croquisTimeDB,
                    })
                    setCroquisTime(doc.data().croquisTimeDB)
                console.log(doc.data())
                console.log(croquisTime)
                time.current+=doc.data().croquisTimeDB-60;
                
               
                
                
            } else {
                // doc.data() will be undefined in this case
                console.log("No such setting document!");
            }
        }).catch((error) => {
            console.log("Error getting document:", error);
             
        });
    }
    const updateSetting =(event)=>{
      // console.log(drawSetting.croquisTimeDB,typeof(drawSetting.croquisTimeDB))
      // console.log(drawSetting.brushRadius,typeof(drawSetting.brushRadius))
      // console.log(drawSetting.lazyRadius,typeof(drawSetting.lazyRadius))
      // console.log(drawSetting.eraseRadius,typeof(drawSetting.eraseRadius))
        dbService.collection("usersetting").doc(userObj.uid).update({
            brushRadius:drawSetting.brushRadius,
            lazyRadius:drawSetting.lazyRadius,
            croquisTimeDB:drawSetting.croquisTimeDB,
            eraseRadius:drawSetting.eraseRadius,
            
        });
    }
    const refreshAndSubmit =()=>{
        setPinreload(pinReload+1);
            
            setPinID(pinID+1);
            //console.log("timeout",randomPinNum[pinID])
            
            time.current=croquisTime;
            
            const data = canvasRef.current.getSaveData();
            const dataUrl=canvasRef.current.getDataURL();
            canvasRef.current.clear();
        
            
            dbService.collection("Csubmit").add({
                text:data,
                createdAt: Date.now(),
                creatorId: userObj.uid,
                drawingTime:croquisTime-min*60-sec,
                heart:(0),
                dataUrl:dataUrl,
                refPinUrl:randomPinNum[pinID]
               //  refPhoto:{refPhoto},
                //pinNumber:randomPinNum,
            });
            
            setCsubmit(""); 

            //clearInterval(timeId.current);
    }
    useEffect(()=> {
      
      getMyCroquisSettings();
     
        timeId.current= setInterval(()=>{
            setMin(parseInt(time.current/60));
            setSec(time.current % 60);
            time.current -= 1;
        },1000);
        return()=> clearInterval(timeId.current);
    },[]);

    useEffect(()=> {
        if(time.current<=-1){
            refreshAndSubmit();
            
            
        }
    },[sec]);

    useEffect(()=> {
      
             
     
       
             dbService.doc(`RefDB/refStrings`).get().then((doc) => {
            if (doc.exists) {
                //console.log("Document data:", doc.data().refUrl);
                //setRandomPinNum(doc.data().refUrl);
                //console.log("doc",doc.data().refUrl)
                const random=doc.data().refUrl.sort(() => Math.random() - 0.5);
                
                
                setRandomPinNum(random);
               
                
                
            } else {
                // doc.data() will be undefined in this case
                console.log("No such document!");
            }
        }).catch((error) => {
            console.log("Error getting document:", error);
             
        });


       
        // dbService.collection("Csubmit").onSnapshot((snapshot) =>{
        //     const CsubmitArray = snapshot.docs.map((doc)=>({
        //         id: doc.id,
        //         ...doc.data(),
        //     }));
            
        //     setCsubmits(CsubmitArray);
        //     //console.log(Csubmits);
        // });

        
        

    },[]);
     const onSubmit= (event) => {
         event.preventDefault();
        //  const data = canvasRef.current.getSaveData();
        //  canvasRef.current.clear();
     
         
        //  await dbService.collection("Csubmit").add({
        //      text:data,
        //      createdAt: Date.now(),
        //      creatorId: userObj.uid,
        //      heart:(0),
        //     //  refPhoto:{refPhoto},
        //      //pinNumber:randomPinNum,
        //  });
        //  setCsubmit(""); 
        refreshAndSubmit();
     };
     const onSubmitPinNum= (event) => {
        event.preventDefault();
        
        if(pinNum.match(",")){
            
            const pinDocRef= dbService.doc(`RefDB/refStrings`);
         
            pinNum.split(",").forEach(e => {
                console.log(e)
                pinDocRef.update({
                    refUrl:firebase.firestore.FieldValue.arrayUnion(e)
            }); 
            
        });
        }else{
            const pinDocRef= dbService.doc(`RefDB/refStrings`);
            pinDocRef.update({
            refUrl:firebase.firestore.FieldValue.arrayUnion(pinNum)
        });
        }

        
           
        setPinNum("")

     }
     const onChange = (event)=> {
         const {
             target:{value},
         }=event;
         setPinNum(value);
     }
     const onChangeTime = (event)=> {
         const {
             target:{value},
         }=event;
         setCroquisTime(value);
         setDrawSetting(prevState=>({...prevState, croquisTimeDB: event.target.value }))
         
     }

     const onSubmitTime= (event) => {
        event.preventDefault();
        setTimeSetBottonToggle(false)
        
        //setCroquisTime(0)
     }

     const onClearClick= () => {
        canvasRef.current.clear();
    };
     const onUndoClick= () => {
        canvasRef.current.undo();
    };
     const onEraseClick= () => {
        setBrushColor("erase")
        
    };
     const onBrushClick= () => {
        setBrushColor("#444")
        
    };

    function ordinal_suffix_of(i) {
        var j = i % 10,
            k = i % 100;
        if (j == 1 && k != 11) {
            return i + "st";
        }
        if (j == 2 && k != 12) {
            return i + "nd";
        }
        if (j == 3 && k != 13) {
            return i + "rd";
        }
        return i + "th";
    }
    function sexagesimal(i) {
        if(i <= 9){
            return "0"+i;
        }else{
            return i;
        }
    }
    //  const onChange = (event)=> {
    //      const {
    //          target:{value},
    //      }=event;
    //      setCsubmit(value);
    //  }

    //  const onSubmitImage= async(event) => {
    //     event.preventDefault();
    //     const fileRef = storageService.ref().child(`${userObj.uid}/${uuidv4()}`);
    //     const response = await fileRef.putString(imageString,"data_url");
    //     const imageUrl =await response.ref.getDownloadURL();
    //     setImageString("");
    //  }
    //  const onFileChange= (event)=>{
    //     const {
    //         target:{files}
    //     }= event;
    //     const theFile = files[0];
    //     const reader= new FileReader();
    //     reader.onloadend = (finishedEvent)=>{
    //         const {
    //             currentTarget:{result},
    //         }=finishedEvent;
            
    //         setImageString(result);
    //     }
    //     reader.readAsDataURL(theFile);
    //  }

  return(
    <><Hotkeys
      keyName="ctrl+z,c"
      onKeyDown={(keyName, e, handle) => {
        //console.log("test:onKeyDown", keyName);
        if (keyName == "ctrl+z") { onUndoClick(); }
        if (keyName == "c") {
          if (brushColor != "erase") { onEraseClick(); }
          else if (brushColor == "erase") { onBrushClick(); }
        }
      } }
    ></Hotkeys>
    <div>
        {/* <PieChart style={{ height: '35px' }}
    data={[{ value: 1, key: 1, color: '#E38627' }]}
    startAngle={-90}
    reveal={-100*(min*60+sec)/croquisTime}
    lineWidth={100}
    // background="#f3f3f3"
    lengthAngle={360}
    /> */}
        <div style={{ float: 'right' }}>
          {timeSetBottonToggle ? (<>
            <form onSubmit={onSubmitTime}>
              <input value={croquisTime} onChange={onChangeTime} type={"number"} placeholder="Input Croquis Time" />
              <input type={"submit"} value="Set Time" />

            </form>
          </>) : (<>
            {ordinal_suffix_of(pinID + 1)}
            <button onClick={(e) => { setTimeSetBottonToggle(true); } }>
              Set Croquis Time</button>

          </>)}
          <button onClick={updateSetting}>Update Setting</button>
          {/* <input
          type="number"
          min="0"
          max="1000"
          style={{ width: "50px" }}
          value={drawSetting.pinWidth}
          onChange={e =>
            setDrawSetting(prevState=>({...prevState, pinWidth: e.target.value }))
          }
        />
        <input
          type="number"
          min="0"
          max="1000"
          style={{ width: "50px" }}
          value={drawSetting.pinHeight}
          onChange={e =>
            setDrawSetting(prevState=>({...prevState, pinHeight: e.target.value }))
          }
        />
        <input
          type="number"
          min="0"
          max="1000"
          style={{ width: "50px" }}
          value={drawSetting.canvasWidth}
          onChange={e =>
            setDrawSetting(prevState=>({...prevState, canvasWidth: e.target.value }))
          }
        />
        <input
          type="number"
          min="0"
          max="1000"
          style={{ width: "50px" }}
          value={drawSetting.canvasHeight}
          onChange={e =>
            setDrawSetting(prevState=>({...prevState, canvasHeight: e.target.value }))
          }
        /> */}
        </div>
        <div style={{
          position: 'absolute',
          left: '50%',
          transform: 'translateX(-50%)',
          width: '70px',
          textAlign: 'center',

          background: 'linear-gradient(to left,#FFC365 ' + (min * 60 + sec) * 100 / croquisTime + '%,#E8E8E8 ' + (min * 60 + sec) * 100 / croquisTime + '%)'
        }}>
          {min}: {sexagesimal(sec)}
        </div>




        <div>
          <form onSubmit={onSubmitPinNum}>
            <input value={pinNum} onChange={onChange} type={"text"} placeholder="You Can Upload Pinterst Pin By Url Number" />
            <input type={"submit"} value="Upload Pin" />

          </form>




        </div>


        {/* <div>https://assets.pinterest.com/ext/embed.html?id={randomPinNum[pinID]}    {pinID}</div> */}
        <div style={{ float: 'left' }}>
          <iframe key={pinReload} src={"https://assets.pinterest.com/ext/embed.html?id=" + randomPinNum[pinID]} height={770} width={400} frameBorder={0} scrolling="no"></iframe>
        </div>

        <div style={{ float: 'left' }}>
          <div>



            <div style={{ float: 'right' }}>
              <label>Lazy:</label>
              <input
                type="number"
                min="0"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.lazyRadius}
                onChange={e => setDrawSetting(prevState => ({ ...prevState, lazyRadius: e.target.value }))} />
            </div>
            <div style={{ float: 'right' }}>
              {brushColor=="erase" ? (<>
                <label>Erase Size:</label>
              <input
                type="number"
                min="1"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.eraseRadius}
                onChange={e => setDrawSetting(prevState => ({ ...prevState, eraseRadius: e.target.value }))} />

              </>):(<>
                <label>Brush Size:</label>
              <input
                type="number"
                min="1"
                max="100"
                style={{ width: "30px" }}
                value={drawSetting.brushRadius}
                onChange={e => setDrawSetting(prevState => ({ ...prevState, brushRadius: e.target.value }))} />
              
              </>)}
              


            </div>
            <div style={{ float: 'right' }}>
              <button onClick={onClearClick}>Clear Canvas</button>
            </div>
            <div style={{ float: 'right' }}>
              <button onClick={onUndoClick}>Undo</button>
            </div>

            <div style={{ float: 'right' }}>
              {(brushColor == "erase") ? (
                <><button onClick={onBrushClick}>brush</button></>
              ) : (<><button onClick={onEraseClick}>erase</button></>)}


            </div>


            <div style={{ float: 'right' }}>
              <form onSubmit={onSubmit}>

                {/* <input value={Csubmit} onChange= {onChange} type={"text"} placeholder="what'on your mind?" maxLength={120} /> */}
                <input type={"submit"} value="Upload Drawing" />

              </form>
            </div>


          </div>
          <div style={{ float: 'right' }}>
            <CanvasDraw
              canvasWidth={600}
              canvasHeight={600}
              brushRadius={(brushColor=="erase") ? drawSetting.eraseRadius : drawSetting.brushRadius}
              lazyRadius={drawSetting.lazyRadius}
              ref={canvasRef}
              brushColor={brushColor}

              allowOnlyPointerType={allowPointerType[pointerTypeIndex]} />
          </div>
        </div>



        {/* <form onSubmit={onSubmitImage}>
          <input type={"file"} accept="image/*" onChange={onFileChange}/>
          <input type={"submit"} value="Upload Croquis Images"/>
    </form> */}





        {/* <div>
        {Csubmits.map((Csubmit)=>(
            <CCsubmit key={Csubmit.id} CsubmitObj={Csubmit} isOwner={Csubmit.creatorId=== userObj.uid} />
        ))}
    </div> */}
      </div></>
  )
 };
 export default Home;